import React from 'react'
import s from "./tab2.module.css"
import Input from '../../../ui/input.jsx';

function Tab2() {
    return ( 
        <>
        <span>Advantages</span>
            <Input/>
            <Input/>
            <Input/>
            <button  to="/auth" id="add" className={s.plus}>+</button>

            <span>Checkbox group</span>
            <label>
            <input id="field-checkbox-group-option-1"type="checkbox" /> 1
            </label>
            <label>
            <input id="field-checkbox-group-option-2"type="checkbox" /> 2
            </label>
            <label>
            <input id="field-checkbox-group-option-3"type="checkbox" /> 3
            </label>
            <span>Radio group</span>
            <label>
            <input name='radio' id="field-radio-group-option-1" type="radio" value='1'/> 1
            </label>   
            <label>
            <input name='radio' id="field-radio-group-option-1" type="radio" value='2'/> 2
            </label>
            <label> 
            <input name='radio' id="field-radio-group-option-1" type="radio" value='3'/> 3
            </label>
        </>
     );
}

export default Tab2;