import React ,{useState} from 'react';
import { Link,useNavigate } from 'react-router-dom';
import s from './create.module.css';
import {useDispatch, useSelector} from 'react-redux'
import Metric from '../../ui/metric/metric';
import Tab1 from './Tab_1/tab1';
import Tab2 from './Tab_2/Tab2';
// import { NICK } from '../../redux/types';

function Create() {
    const navigate = useNavigate();
    const state = useSelector(state=> state.profile);
    console.log(state);
    const [width, valueChange] = useState(0);
    // let width = 0;
    const [tab, setTab]= useState(1);
    const[btn,SetBtn] = useState('Вперед');
    return (
        <div  className={s.container}>
            <Metric width={width} tab={tab}/>
            <form action="">
                {tab === 1 ?<Tab1 /> : null}
                {tab === 2 ?<Tab2 /> : null}
            </form>
            <button  id="button-back" className='btn btn_back' onClick={()=>{
                            if (tab === 1) {
                                navigate(-1);
                            }
                            else {
                                setTab(tab-1);
                            }
                        }}
                >Назад
            </button>
            <button  id="button-next" className='btn' onClick={(e)=>{
                valueChange(width+1);
                    console.log(width);
                    setTab(tab+1);
                    if (tab === 2){
                        SetBtn('Отправить')
                    } else {
                        SetBtn('Далее')
                    }

                // switch (tab) {

                // case 1 :
                // if (width !== {width: "100%"})valueChange({width: "50%"});
                // setTab(tab+1);
                // break;
                // case 2 :
                //     valueChange({width: "100%"});
                //     setTab(tab+1);
                //     SetBtn('Отправить')
                // break;
                // }

                    }} 
                >{btn}
            </button>
        </div>
    );
}

export default Create;